.slide-container {
  width: 100%;
  margin: auto;
}

h3 {
  text-align: center;
}

.each-slide{
  width: 100%;
  background-color: #5c5874c9;
}

.each-slide > div {
  /* display: block; */
  /* align-items: center;
  justify-content: center; */
  background-position: center;
  background-repeat: no-repeat;
  /* background-size: cover; */
  height:500px;
  width:100%
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.each-fade {
  display: flex;
}

.each-fade .image-container {
  /* width: 100%; */
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
}

.each-fade h2 {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background: #adceed;
}
